import Vue from 'vue'
import Router from 'vue-router'
import Default from '@/components/default/Default.vue'
import Blank from '@/components/default/Blank.vue'

const index = ()=> import('@/views/index.vue')
const about = ()=> import ('@/views/about.vue')
const message = ()=> import('@/views/message.vue')
const sitemap = ()=> import('@/views/sitemap.vue')

Vue.use(Router)

export default new Router({
  mode:'history',
  linkActiveClass:'active',
  routes: [
    {
      path: '/',
      name: 'default',
      component: Default,
      redirect:'/index',
      children:[
        {
          path:'/index',
          name:'index',
          component:index,
        },
        {
          path:'/about',
          name:'about',
          component:about
        },
        {
          path:'/message',
          name:'message',
          component:message
        },{
          path:'/detail',
          name:'detail',
          component:() => import('@/views/detail')
        }
      ]
    },
    {
      path:'/blank',
      name:'blank',
      component:Blank,
      redirect:'/sitemap',
      children:[
        {
          path:'/sitemap',
          name:'sitemap',
          component:sitemap
        }
      ]
    }
  ]
})