<template>
    <div class="latest-comment">
        <random-title :title="title"/>
        <ul class="comment-list">
            <li v-for="item in commentList" :key="item.comment">
                <div>
                    <span>{{item.user}}</span>
                    <span>{{item.time}}</span>
                </div> 
                <a href="#">{{item.comment}}</a>
            </li>
        </ul>
    </div>
</template>


<script>
import randomTitle from './randomTitle.vue'
export default {
    components:{
        randomTitle
    },
    data(){
        return {
            commentList:[
                // {user:'杰西',time:'[5天前]',comment:'站长可以加个友链么，我新站，刚出来没'},
                // {user:'小刚刚',time:'[3周前]',comment:'大吉大利'},
                // {user:'鸟叔',time:'[4周前]',comment:'都能反汇编微信了，厉害'},
                // {user:'秃顶长发大波浪',time:'[1个月前]',comment:'给个联系方式，深入交流一下？'},
                // {user:'ZL',time:'[1个月前]',comment:'unoconv-convert-do'},
                // {user:'ZL',time:'[1个月前]',comment:'你可以看看这篇，unoconv-coo'}   
            ],
            title:'最新评论',
            size:3
        }
    },
    mounted(){
        this.$axios.get('/queryNewComments',{
            params:{
                size:this.size
            }
        }).then(res =>{
            const result = res.data.data;
            const list = [];
            for(let i = 0; i < result.length; i++){
                const temp = {};
                temp.user = result[i].user_name;
                temp.time = result[i].ctime;
                temp.comment = result[i].comments;
                list.push(temp);
            }
            this.commentList = list;
        }).catch(err => console.log(err))
    }

}
</script>


<style lang="scss">
    @import "@/assets/css/default/latestComments.scss"
</style>