<template>
    <div class="random-tags">
        <random-title :title="title"/>
        <div class="tags-list">
            <!-- 此处不用router-link,需要刷新页面 -->
            <a :href="`/index?tag=${item}`" v-for="(item,index) in randomData" :key="index" 
                :style="{'color':randomColor(),'font-size':randomFont()}">{{item}}</a>
        </div>
    </div>
</template>


<script>
import randomTitle from './randomTitle.vue'
export default {
    components:{
        randomTitle
    },
    data(){
        return {
            tagList:[],
            title:'随机标签云'  
        }
    },
    mounted(){
        this.getAllTags()
    },
    methods:{
        getAllTags(){
            this.$axios.get('/queryAllTags',{
                params:{}
            }).then(res =>{
                const result = res.data.data;
                const list = [];
                for(let i = 0; i < result.length; i++){
                    list.push(result[i].tag);
                }
                this.tagList = list.sort((a,b) => Math.random() > 0.5)
            }).catch(err => console.log(err));
        }
    },
    computed:{
        randomData(){
            return this.tagList.sort(function(a,b){
                return Math.random() > 0.5;
            });
        },
        randomColor(){
            return function(){
                let r = Math.floor(Math.random()*256);
                let g = Math.floor(Math.random()*256);
                let b = Math.floor(Math.random()*256);
                return `rgb(${r},${g},${b})`
            }
        },
        randomFont(){
            return function(){
                let font = Math.floor(Math.random()*10 + 12);
                return `${font}px`
            }
        },
    }
}
</script>


<style lang="scss">
    @import "@/assets/css/default/randomTags.scss"
</style>