<template>
    <div class="recent-hot">
        <random-title :title="title"/>
        <ul class="hot-list">
            <li v-for="item in hotList" :key="item.title">
                <a :href="item.link">{{item.title}}</a>
            </li>
        </ul>
    </div>
</template>


<script>
import randomTitle from './randomTitle.vue'
export default {
    components:{
        randomTitle
    },
    data(){
        return {
            hotList:[],
            title:'最近热门',
            size:6
        }
    },
    mounted(){
        this.$axios.get('/queryHotBlogBySize',{
            params:{
                size:this.size
            }
        }).then(res =>{
            const result = res.data.data;
            const list = [];
            for(let i = 0; i < result.length; i++){
                const temp = {};
                temp.title = result[i].title;
                temp.link = `/detail/?bid=${result[i].id}`;
                list.push(temp);
            }
            this.hotList = list;
        }).catch(err => console.log(err))
    }

}
</script>


<style lang="scss">
    @import "@/assets/css/default/recentHots.scss"
</style>