<template>
    <!-- <div> -->
        <canvas class="canvas"></canvas>
    <!-- </div> -->

</template>

<script>
export default{
    mounted(){
        var canvas = document.getElementsByClassName('canvas')[0];
        var ctx = canvas.getContext('2d'); 
        var w;
        var h;
        var nodes = [];
        var edges = [];
        // var easingFactor = 5.0;
        // mousePos = [0, 0];
        init();
        function init() {
            console.log(canvas.parentElement)
            console.log(canvas.parentElement.offsetWidth,window.innerWidth,canvas.parentElement.offsetHeight)
            w = canvas.width = canvas.parentElement.offsetWidth || window.innerWidth;
            h = canvas.height = canvas.parentElement.offsetHeight || window.innerHeight;
        }
        window.onresize = init;

        function Node() {
            this.x = rand(0, w);
            this.y = rand(0, h);
            this.vx = rand(-0.5, 0.5);
            this.vy = rand(-0.5, 0.5);
            this.radius = (Math.random() > 0.9 ? rand(3, 6) : rand(1, 4));
        }

        Node.prototype.draw = function () {
            ctx.fillStyle = '#0ff';
            ctx.beginPath();
            ctx.arc(this.x, this.y, this.radius, 0, 2 * Math.PI, 0);
            ctx.closePath();
            ctx.fill();
            this.update();
        }

        Node.prototype.update = function () {
            this.x += this.vx;
            this.y += this.vy;
            
            if (this.x <= 0) {
                this.vx *= -1;
                this.x = 0;
            }
            if (this.x >= w) {
                this.vx *= -1;
                this.x = w;
            }
            if (this.y <= 0 ) {
                this.vy *= -1;
                this.y = 0;
            }
            if (this.y >= h) {
                this.vy *= -1;
                this.y = h;
            }
        }

        for (var i = 0; i < 100; i++){
            nodes.push(new Node());
        }

        function Edge(from, to) {
            this.from = from;
            this.to = to;
        }

        Edge.prototype.draw = function () {
            var len = getLength(this);
            var rangeLen = w / 12;
            if (len > rangeLen) {
                return;
            }
            ctx.lineWidth = (1 - len / rangeLen) * 2;
            ctx.globalAlpha = 1 - len / rangeLen;
            // ctx.strokeStyle = getRandColor();
            ctx.strokeStyle = '#0ff';
            ctx.beginPath();
            ctx.moveTo(this.from.x, this.from.y);
            ctx.lineTo(this.to.x, this.to.y);
            ctx.closePath();
            ctx.stroke();
        }
        nodes.forEach(item1 => {
            nodes.forEach(item2 => {
                if (item1 == item2) {
                    return;
                }
                var edge = new Edge(item1, item2);
                addEdge(edge);
            })
        })

        function addEdge(edge) {
            var ignore = false;
            edges.forEach(ele => {
                if ((ele.from == edge.from && ele.to == edge.to) || (ele.from == edge.to && ele.to == edge.from)) {
                    ignore = true;
                }
            })
            if (!ignore) {
                edges.push(edge);
            }
        }

        render();
        function render() {
            ctx.clearRect(0, 0, w, h);
            nodes.forEach(item => {
                item.draw();
            })
            edges.forEach(function (elem) {
                elem.draw();
            })
            ctx.globalAlpha = 1;
            requestAnimationFrame(render);
        }

        window.onmousemove = function (e) {
            nodes[0].x = e.pageX;
            nodes[0].y = e.pageY;
            // mousePos[0] = e.pageX;
            // mousePos[1] = e.pageY;
            // nodes[0].x += (mousePos[0] - nodes[0].x) / easingFactor;
            // nodes[0].y += (mousePos[1] - nodes[0].y) / easingFactor;
        }

        function getLength(e) {
            return Math.sqrt(Math.pow(e.to.x - e.from.x, 2) + Math.pow(e.to.y - e.from.y, 2));
        }

        function getRandColor() {
            var r = Math.floor(Math.random() * 256);
            var g = Math.floor(Math.random() * 256);
            var b = Math.floor(Math.random() * 256);
            return 'rgb(' + r + ',' + g + ',' + b + ')';
        }
        function rand(min, max) {
            return Math.random() * (max - min + 1) + min;
        }

    }
}
</script>

<style scoped>
    canvas{
        position:absolute;
        top:0;
        left:0;
        background:#eee;
        z-index:1;
        /* background:#ccc; */
        /* display:block; */
    }
</style>