<template>
    <div class="title" :style="{'color':color}" >
        {{title}}
    </div>
</template>


<script>
export default {
    props:['title'],
    data(){
        return{
            color:'',
            num:0,
            timer:null
        }
    },
    created(){
        clearInterval(this.timer)
        this.timer = setInterval(()=>{
            this.num++;
            if(this.num > 255){
                this.num = 0;
            }
            this.color = this.changeColor(this.num);
        },50)
    },
    computed:{
        changeColor(){
            return function(x){
                return `rgb(${x},${135},${255})`
            }    
        }
    }

}
</script>


<style lang="scss">
    @import "@/assets/css/default/randomTitle.scss"
</style>